import React, { useEffect } from 'react'
import './Title.css'
// import $ from 'jquery'
import { Route } from 'react-router-dom'

const Title = () => {
   useEffect(() => {
      // $('.hpscroll').click(function () {
      //    $('html,body').animate(
      //       {
      //          scrollTop: $('.Featuredtext').offset().top,
      //       },
      //       'fast'
      //    )
      // })
   })
   return (
      <div className="title">
         <div className="header">
            <div
               id="hero-text"
               // data-scroll-direction="horizontal"
            >
               <h1 className="titletext">
                  Brand By Agence is a global creative branding studio founded
                  in India. We make brands future-ready.
               </h1>
               <div>
                  <Route
                     render={({ history }) => (
                        <button
                           className="button"
                           id="abc"
                           data-scroll
                           data-scroll-speed="-1"
                           onClick={() => {
                              history.push('/about')
                           }}
                        >
                           Read more
                        </button>
                     )}
                  />
               </div>
            </div>
            <div className="hpscroll">
               <span>scroll</span>
               <div className="scrolll"></div>
            </div>
         </div>
         <img
            className="spaceship"
            src={require('../../../bulb-min.png')}
            alt=""
         />
         {/* <img
            className="homw im"
            src={require('../../../characters-min.png')}
            alt=""
         />
         <img
            className="plants"
            src={require('../../../plants-min.png')}
            alt=""
         /> */}
      </div>
   )
}

export default Title
